import React, { Component } from "react";
import "./styles/styles.css";
import Header from "./components/Header";
import MainContent from "./components/MainContent";
import Footer from "./components/Footer";
import productData from "./data/productData";

class App extends Component {
  constructor() {
    super();
    this.state = {
      data: productData,
      cartNum: 0,
    };
    this.handleAddToCart = this.handleAddToCart.bind(this);
    this.handleRemoveFromCart = this.handleRemoveFromCart.bind(this);
    this.handleAddQty = this.handleAddQty.bind(this);
    this.handleSubQty = this.handleSubQty.bind(this);
    this.handleCheckout = this.handleCheckout.bind(this);
  }

  handleAddToCart(id) {
    let cartAdder = 0;
    const updatedData = this.state.data.map((product) => {
      if (product.id === id) {
        if (product.inCart === false) {
          product.inCart = true;
          cartAdder = 1;
          alert(product.title + " have been added to your cart.");
        } else alert(product.title + " are already in your cart.");
      }
      return product;
    });
    this.setState((prevState) => ({
      data: updatedData,
      cartNum: prevState.cartNum + cartAdder,
    }));
  }

  handleRemoveFromCart(id) {
    let cartRemover;
    const updatedData = this.state.data.map((product) => {
      if (product.id === id) {
        product.inCart = false;
        cartRemover = product.qty;
        product.qty = 1;
      }
      return product;
    });
    this.setState((prevState) => ({
      data: updatedData,
      cartNum: prevState.cartNum - cartRemover,
    }));
  }

  handleAddQty(id) {
    let cartAdder = 1;
    const updatedData = this.state.data.map((product) => {
      if (product.id === id) {
        if (product.qty >= 10) {
          cartAdder = 0;
          alert(
            "There is a limit of 10 " + product.title + " per cart. Thank you."
          );
        } else product.qty = product.qty + 1;
      }
      return product;
    });
    this.setState((prevState) => ({
      data: updatedData,
      cartNum: prevState.cartNum + cartAdder,
    }));
  }

  handleSubQty(id) {
    const updatedData = this.state.data.map((product) => {
      if (product.id === id) {
        if (product.qty <= 1) {
          product.inCart = false;
        } else product.qty = product.qty - 1;
      }
      return product;
    });
    this.setState((prevState) => ({
      data: updatedData,
      cartNum: prevState.cartNum - 1,
    }));
  }

  handleCheckout() {
    const checkoutData = this.state.data.map((product) => {
      if (product.inCart === true) {
        product.inCart = false;
        product.qty = 1;
      }
      return product;
    });
    alert("Thank you for your purchase.");
    this.setState({
      data: checkoutData,
      cartNum: 0,
    });
  }

  render() {
    return (
      <div>
        <Header
          cartNum={this.state.cartNum}
          handleRemoveFromCart={this.handleRemoveFromCart}
          handleAddQty={this.handleAddQty}
          handleSubQty={this.handleSubQty}
          handleCheckout={this.handleCheckout}
        />
        <MainContent handleAddToCart={this.handleAddToCart} />
        <Footer />
      </div>
    );
  }
}

export default App;
