import React, { Component } from "react";
import Logo from "../images/logo.png";
import ShoppingCart from "./ShoppingCart";
import productData from "../data/productData";

class Header extends Component {
  constructor() {
    super();
    this.state = {
      cartOpen: false,
      data: productData,
    };
    this.handleOpen = this.handleOpen.bind(this);
    this.handleClose = this.handleClose.bind(this);
  }

  handleOpen() {
    this.setState({ cartOpen: true });
  }

  handleClose() {
    this.setState({ cartOpen: false });
  }

  render() {
    const logo = <img className="logo" src={Logo} alt="Jango Chocolate" />;

    const shoppingCart = this.state.cartOpen ? (
      <ShoppingCart
        cartNum={this.props.cartNum}
        handleClose={this.handleClose}
        handleRemoveFromCart={this.props.handleRemoveFromCart}
        handleAddQty={this.props.handleAddQty}
        handleSubQty={this.props.handleSubQty}
        handleCheckout={this.props.handleCheckout}
      />
    ) : null;

    return (
      <header>
        <div className="header-top">
          {logo}
          <div className="header-right">
            <div className="cart" onClick={this.handleOpen}>
              <i className="fas fa-shopping-cart"></i>
              <span>{this.props.cartNum}</span>item(s)
            </div>
            <div className="search">
              <input type="text" placeholder="Search" />
              <i className="fas fa-search"></i>
            </div>
          </div>
        </div>
        <nav>
          <div className="navbar">
            <ul className="navigation">
              <li>chocolate</li>
              <li>recipes</li>
              <li>about us</li>
              <li>s'more</li>
            </ul>
            <ul className="social">
              <li>careers</li>
              <li>
                connect: <i className="fab fa-facebook-f"></i>
                <i className="fab fa-instagram"></i>
                <i className="fab fa-pinterest-p"></i>
              </li>
            </ul>
          </div>
        </nav>
        {shoppingCart}
      </header>
    );
  }
}

export default Header;
